const app = getApp();
export default {
	data() {
		return {
			source: 0,
			addressList: []
		}
	},
	onLoad(option){
		console.log(option.source);
		this.source = option.source;
		this.loadData();
	},
	methods: {
		async loadData(){
			let resList = await app.request(this.$Url + "/clmsj/Address/find_address_list", {});
			if (resList.data.code == 0) {
				this.addressList = resList.data.data.address_list;
			}
			if (resList.data.code == 2) {
				uni.showToast({
					icon:'none',
					title:'请完善您的收货信息'
				})
			}
		},
		chooseAddress(){
			let _this = this;
			uni.chooseAddress({
				success(res) {
					console.log(res.userName)
					console.log(res.postalCode)
					console.log(res.provinceName)
					console.log(res.cityName)
					console.log(res.countyName)
					console.log(res.detailInfo)
					console.log(res.nationalCode)
					console.log(res.telNumber)
					
					app.request(_this.$Url + "/clmsj/Address/add_address", {
						name : res.userName,
						phone : res.telNumber,
						province : res.provinceName,
						city : res.cityName,
						area : res.countyName,
						address : res.provinceName + res.cityName + res.countyName + res.detailInfo
					}).then(resAdd=>{
						if (resAdd.data.code == 0) {
							uni.showToast({
								title:'获取成功'
							})
							_this.loadData();
						}
					});
					
					
				}
			})
		},
		delAddress(item){
			let _this = this;
			uni.showModal({
			    title: '提示',
			    content: '您确认要删除吗？',
			    success: function (res) {
					console.log(res)
					if(res.confirm){
						app.request(_this.$Url + "/clmsj/Address/del_address", {
							id : item.id
						}).then(resAdd=>{
							if (resAdd.data.code == 0) {
								uni.showToast({
									title:'删除成功'
								})
								_this.loadData();
							}
						});
					}
				}
			})
			
		},
		//选择地址
		checkAddress(item){
			if(this.source == 1){
				//this.$api.prePage()获取上一页实例，在App.vue定义
				this.$api.prePage().addressData = item;
				uni.navigateBack()
			}
		},
		addAddress(type, item){
			uni.navigateTo({
				url: `/pages/address/addressManage?type=${type}&data=${JSON.stringify(item)}`
			})
		},
		//添加或修改成功之后回调
		refreshList(data, type){
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			this.addressList.unshift(data);
			
			console.log(data, type);
		}
	}
}
